function styleInject(css, ref) {
  if ( ref === void 0 ) ref = {};
  var insertAt = ref.insertAt;

  if (!css || typeof document === 'undefined') { return; }

  var head = document.head || document.getElementsByTagName('head')[0];
  var style = document.createElement('style');
  style.type = 'text/css';

  if (insertAt === 'top') {
    if (head.firstChild) {
      head.insertBefore(style, head.firstChild);
    } else {
      head.appendChild(style);
    }
  } else {
    head.appendChild(style);
  }

  if (style.styleSheet) {
    style.styleSheet.cssText = css;
  } else {
    style.appendChild(document.createTextNode(css));
  }
}

var css = "/* GRID */\n.osome-cal-grid-week {\n    position: relative;\n    min-height: 120px;\n}\n.osome-cal-grid-week-grid {\n    position: absolute;\n    display:block;\n    width:100%;\n    height:100%;\n    background-color:transparent;\n    z-index: 10;\n}\n.osome-cal-grid-week-schedule {\n    position: absolute;\n    display:block;\n    width:100%;\n    padding-top:20px;\n}\n\n.osome-cal-grid-week-schedule .event-block {\n    position: relative;\n    z-index:11;\n    display:block;\n    cursor: move;\n}\n.event-block .title {\n    display: block;\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    padding-left: 3px;\n    font-weight: 700;\n    -moz-user-select: none;\n    -webkit-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n    -o-user-select: none;\n}\n.block-right .resize-handle {\n    display:none;\n}\n.event-block .resize-handle {\n    position: absolute;\n    top: 0;\n    right: 0;\n    width: 6px;\n    line-height: 24px;\n    cursor: col-resize;\n    z-index:11;\n}\n.event-block .resize-handle.handler-y {\n    background-color:lightgray;\n    width: 6px;\n    height:100%;\n    border:solid 1px #acacac;\n    \n}   \n\n/* */\n.osome-cal-grid-week-grid .tile {\n    min-height: 120px;\n    height:100%;\n    -moz-user-select: none;\n    -webkit-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n    -o-user-select: none;\n}\n\n.osome-cal-grid-week-grid .tile.dragOver {\n    background-color: rgba(223, 230, 233,0.5)\n}\n\n.osome-cal-grid-week-grid .tile.active {\n    background-color: rgba(81, 92, 230, 0.05)\n}\n\n.osome-cal-grid-week-grid .tile.prev {\n    color: #aeaeae;\n}\n\n.osome-cal-grid-week-grid .tile.next {\n    color: #aeaeae;\n}\n\n.dragImage {\n    position: absolute;\n    left: -100%;\n}\n\n\n#osome-cal-days .column {\n    padding: 0;\n    height: 32px;\n    line-height: 32px;\n    text-align: center;\n}\n\n#osome-cal-grid {\n    margin: 0;\n}\n\n.text-blue {\n    color: #2185d0;\n}\n\n.text-red {\n    color: #db2828;\n}\n\n\n/* EVENT POPUP*/\n\n#osome-cal-event-popup {\n    position:absolute;\n    background-color: white;\n    width:700px;\n    padding:30px;\n    z-index:100;\n    border-radius: 5px;\n    box-shadow: 0px 0px 10px 1px lightgray;\n}\n#osome-cal-event-popup .ui.form {\n    padding:20px;\n\n}\n\n#osome-cal-event-popup i.close{\n    cursor: pointer;\n    position: absolute;\n    right:25px;\n}";
